:root {
    --main-color :#FF6060;
    --main-font : 'Montserrat', sans serif;
     --main-background-color : #f6f6f6;
 }

 p {
    margin-block : 0rem;
 }

 .Description {
    flex-basis :50%;
 }

 .Description__content {
    padding: 1rem;
    color: var(--main-color);
    background-color: var(--main-background-color);
    border-radius: 0.7rem ;
    position : relative;
    top : -0.3rem;
    z-index: -1;
    min-height: 12rem;
}

.Description__about {
    margin: 2rem 100px;
  max-width : 1023px;
    flex-basis :50%;
 }

 .Description__header {
    background-color: var(--main-color);
    color: white;
    padding: 0.2rem 1rem;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.3rem 0rem;
    height: 52px;
}


 .Description__content {
    padding-top: 2rem;
    color: var(--main-color);
    background-color: var(--main-background-color);
    border-radius: 0rem 0rem 0.7rem 0.7rem;
    position: relative;
    top: -1rem;
    z-index: -1;
    margin: 0 0rem;
    font-size: 18px;

}

.Description__content__text {
    padding: 1rem;
    padding-bottom:1rem;
    color: var(--main-color);
    background-color: var(--main-background-color);
    border-radius: 0.7rem ;
    position: relative;
    top: -0.3rem;
    z-index: -1;
    min-height: 82.5%;
    line-height: 142%;
    font-size: 23px;

}

.Description__content li {
    list-style: none;
}


.Description__header i {
    font-size : 2rem;
    cursor : pointer;
}


.Description__list {
    display : flex;
    margin-left : 2rem;
    color :#FF6060;
    font-size: 15px;

}




/************************  RESPONSIVE ******************************/
@media (max-width : 970px) {
    .Description__header {
           margin :0.3rem 1rem 20px 1rem;
           height: 35px;
    }
    }



@media (max-width: 900px) {
    .equipment { 
         margin : 1rem 1rem;
    }



    .Description__content {
        margin : -1rem 1rem;
        font-size : 12px;
    }

}