:root {
    --main-color :#FF6060;
    --main-font : 'Montserrat', sans serif;
     --main-background-color : #f6f6f6;
 }

 p {
    margin-block : 0rem;
 }

 .Collapse__about {
    margin: 2rem 100px;
  max-width : 1023px;
    flex-basis :50%;
 }

 .Collapse__header {
    background-color: var(--main-color);
    color: white;
    padding: 0.2rem 1rem;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.2rem 0;
    font-size : 24px;
    min-height : 47px;
}

.Collapse__header i {
    font-size : 2rem;
    cursor : pointer;
}

 .Collapse__content {
    padding: 1rem;
    color: var(--main-color);
    background-color: var(--main-background-color);
    border-radius: 0.7rem ;
    position : relative;
    top : -0.3rem;
    z-index: -1;
}

.Collapse__content__text {
    padding: 1rem;
    padding-bottom :1rem;
    color: var(--main-color);
    background-color: var(--main-background-color);
    border-radius: 0.7rem ;
    position : relative;
    top : -0.3rem;
    z-index: -1;
    min-height: 82.5%;
    line-height: 142%;
  font-size : 24px;
 
}

.Collapse__content li {
    list-style: none;
}


/************************  RESPONSIVE ******************************/

@media (max-width: 900px) {
    .description__about { 
         margin : 1rem 1rem;
    }

    .Collapse__about {
        margin: 2rem 0rem;
      max-width : 1023px;
        flex-basis :50%;
     }
     
     .Collapse__content__text {
        font-size : 15px;
     }
}